@import url('https://fonts.googleapis.com/css2?family=IBM+Plex+Mono&display=swap');

* {
  background-color: #222831;
  color: #EEEEEE;
  font-family: 'IBM Plex Mono', Courier, monospace;
}

nav {
  box-shadow: 0 5px 15px #1e232b;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

.logo {
  text-decoration: none;
  color: #EEEEEE;
}

.logo:hover {
  font-weight: bold;
  color: #EEEEEE;
}

img {
  width: 80%;
}

.headtitle1 {
  padding-bottom: 2%;
  border-bottom: 3px solid #D65A31;
}

.btn-pri {
  background-color: #D65A31;
  border-radius: 10px;
  padding: 12px;
}

.btn-sec {
  color: #D65A31;
  border: 2px solid #D65A31;
  border-radius: 10px;
  padding: 12px;
}

.btn:hover {
  box-shadow: 0 10px 10px #4c5057;
}

.btn-sec:hover {
  color: #D65A31;
}

.bg-square {
  width: 40px;
  height: 40px;
  background-color: #393E46;
  padding: 5px 0 12px 30px;
}

.about-container {
  padding-top: 8%;
}

.card {
  background-color: transparent;
  border: none;
}

.card:hover {
  background-color: #393E46;
}

.modal {
  background-color: #1b1d22e8;
}

.modal-dialog, .modal-content {
  background: transparent;
}

.modal-content, .modal-body {
  border: none;
  border-radius: 30px;
}

.project-img, .project-img-modal {
  border-radius: 20px;
}

.project-img-modal {
  width: 70%;
}